body {
  background: #f3f3f3;
  font-family: 'Segoe UI', sans-serif;
  line-height: 1.4;
  font-display: fallback;
  margin: 0;
  padding: 15px 20px;
  -webkit-font-smoothing: antialiased;
}

.head {
  padding-bottom: 25px;
  border-bottom: 1px solid #cccccc;
}

.head__heading {
  color: #ff6600;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 8px;
}

.head__sub {
  color: #3d3d3d;
  font-size: 14px;
  font-style: italic;
  margin-bottom: 5px;
}

.head__desc {
  color: #3d3d3d;
  font-size: 13px;
  line-height: 18px;
}

.steps {
  display: flex;
  margin: 0;
  padding: 0;
  padding-top: 17px;
  padding-bottom: 23px;
}

.steps__single {
  flex: 0 0 33%;
}

.steps__single.active .steps__heading {
  color: #ff6600;
}

.steps__single.active .line {
  background-color: #ff6600;
  width: 100%;
}

.steps__single.active .number {
  color: #ff6600;
  border-color: #ff6600;
}

.steps__single.active ~ .steps__single .steps__heading {
  color: #cccccc;
}

.steps__single.active ~ .steps__single .line {
  background-color: #cccccc;
  width: 100%;
}

.steps__single.active ~ .steps__single .number {
  color: #cccccc;
  border-color: #cccccc;
}

.steps__heading {
  color: #0f609f;
  font-size: 13px;
  margin-bottom: 10px;
  text-align: center;
}

.steps__line {
  background-color: #0f609f;
  height: 14px;
  position: relative;
}

.steps__line .line {
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
}

.steps__line .number {
  color: #0f609f;
  font-size: 13px;
  font-weight: 600;
  line-height: 13px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid #0f609f;
  background-color: #f3f3f3;
  border-radius: 50%;
  width: 26px;
  height: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 0;
}

.content__heading {
  color: #5b5b5b;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 12px;
}

.content__radio {
  padding: 0;
  margin: 0;
}

.content__text {
  color: #3d3d3d;
  font-size: 13px;
  font-weight: 600;
  line-height: 16px;
}

.content__single {
  display: none;
  padding-bottom: 15px;
}

.content__single.active {
  display: block;
}

.content__single--first .content--left {
  flex: 0 0 195px;
}

.content__single--first .content--right {
  padding-left: 25px;
}

.content__row {
  display: flex;
}

.footer {
  border-top: 1px solid #cccccc;
  padding-top: 20px;
}

.footer__social {
  display: flex;
  margin: 0;
  padding: 0;
  margin-top: 15px;
}

.footer__social li {
  margin-right: 35px;
}

.footer__social a {
  color: #0f609f;
  font-size: 13px;
  text-decoration: none;
}

.footer__bottom {
  margin-top: 20px;
  border-top: 1px solid #cccccc;
  padding-top: 15px;
  display: none;
}

.footer__bottom_text span {
  color: #ff6600;
}

.footer__next {
  margin-left: auto;
}

.footer__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__prev {
  display: none;
}

input {
  border: 1px solid #b0b0b0;
  background-color: #ffffff;
  color: #3d3d3d;
  font-size: 13px;
  padding: 3px 6px;
  width: 100%;
  max-width: 290px;
}

.radio_item {
  margin-bottom: 8px;
}

.radio_item label {
  cursor: pointer;
}

.radio_item label:before {
  content: '';
  border: 1px solid #0f609f;
  background-color: #ffffff;
  width: 13px;
  margin-right: 10px;
  height: 13px;
  display: inline-block;
  vertical-align: middle;
}

.radio_item label span {
  color: #3d3d3d;
  font-size: 13px;
}

.radio_item input {
  display: none;
}

.radio_item input:checked + span:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  background: red;
  width: 20px;
  height: 20px;
}

.button {
  font-size: 15px;
